import { observer } from 'mobx-react-lite';
import React from 'react';
import { SyntheticEvent } from 'react';
import { Button, Reveal } from 'semantic-ui-react';
import { Profile } from '../../app/models/profile';
import { useStore } from '../../app/stores/store';

interface Props {
      profile : Profile;
}

export default observer(function FollowButton({profile}: Props) {
      const {profileStore, userStore} = useStore();
      const {updateFollowing} = profileStore;

      if (userStore.user?.username === profile.username) return null;

      function handleFollow(e: SyntheticEvent, username: string) {
            e.preventDefault();
            profile.following ? updateFollowing(username, false) : updateFollowing(username, true);
      }
      
      return (
            <Reveal animated='move'>
                  <Reveal.Content visible style={{width:'100%'}}>
                        <Button fluid color='teal' content={profile.following ? 'Following' : 'Not Following'} />
                  </Reveal.Content>
                  <Reveal.Content hiddle style={{width:'100%'}}>
                        <Button fluid basic color={profile.following ? 'red' : 'green'} content={profile.following ? 'Unfollow' : 'Follow'} onClick={(e) => handleFollow(e, profile.username)}/>
                  </Reveal.Content>
            </Reveal>
      )
})